import React from 'react'
import { AppBar, Toolbar, styled } from '@mui/material'
import { height } from '@mui/system'

const Container = styled(AppBar)`
background: #060606;
height: 9vh;
`
const Header = () => {
  return (
   <Container position='static'>
      <Toolbar>
        <h3>P.p Editor</h3>
      </Toolbar>
   </Container>
  )
}

export default Header
